.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg{
    background-color: rgba(0,0,0,0.85);
    transition: 0.5s;
}

.nav-menu{
    display: flex;
}

.nav-menu li{
    padding: 0 1rem;
}

.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
    color: green;
    font-style: italic;
    font-family: fantasy;
    font-size:xx-large;
}

.header h1 {
    color: green;
    font-style: italic;
    font-family: fantasy;
    font-size:xx-large;
}

.header-bg h1{
    color: green;
    font-style: italic;
    font-family: fantasy;
    font-size:xx-large;   
}

.hamburger{
    display: none;
}

.titleLine {
    background-color: green;
    width: 100%;
    height: 1px;
    border-top: 10px dotted black;
    line-height: 80%;
    margin-bottom: 0;
    padding-bottom: 0;
}

.titleLine2 {
    background-color: green;
    width: 100%;
    height: 1px;
    border-top: 10px dotted black;
    line-height: 80%;
    margin-bottom: 0;
    padding-bottom: 0;
}

@media screen and (max-width: 740px){
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.9);
        position: fixed;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }

    .nav-menu.active{
        left: 0;
    }

    .nav-menu li{
        padding: 1rem 0;
    }

    .nav-menu li a {
        font-size: 2rem;
    }

    .hamburger{
        display: initial;
    }
}